@import '../../../../scss/colors.scss';

#price-history-chart {
  position: relative;
  margin-left: 30px;
  display: inline-block;
  margin-top: 20px;
  margin-right: 35px;

  .banner-non-available {
    left: 25%;
    text-align:center;
    top: 45%;
    position: absolute;
    width: 200px;
    font-family: Montserrat;
    font-size: 13px;
    font-weight: 500;
    color: #232E3B;
    border-radius: 13px;
    box-shadow: 0 3px 15px 0 rgba(177, 186, 197, 0.2);
    padding: 5px 15px 5px 15px;
  }
}

.recharts-layer.recharts-cartesian-axis-tick tspan {
  font-size: 14px !important;
  font-family: Montserrat;
  font-weight: 100;
  font-stretch: normal;
  color: #526173;
}
