@import "../../../../scss/colors.scss";

.settings-screen-link {
  width: 100%;
  height: 60px;
  text-align: left;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  border: none;
  text-decoration: none;
  text-align: left;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  color: $dark;
  border-radius: 6px;
  box-shadow: 0 10px 20px -5px rgba(177, 186, 197, 0.2);
  background-color: white;

  .earny-plus-text {
    margin: 0;
  }

  &:focus {
    outline: none;
  }
}
