#earnings {
  display: flex;
  flex-direction: row;

  .earning {
    width: 100%;
    border-radius: 6px;
    box-shadow: 0 5px 20px -5px rgba(177, 186, 197, 0.2);
    padding: 16px 34px 17px 34px;

    img {
      margin-bottom: 6px;
      min-height: 35px;
      width: 35px;
      height: 35px;
    }

    h3 {
      font-size: 12px;
      font-weight: 600;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.42;
      letter-spacing: normal;
      color: #b1bac5;
      margin: 0 0 6px 0;
    }

    h2 {
      font-size: 16px;
      font-weight: 700;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal;
      text-align: center;
      color: #19a388;
      margin: 0;
    }
  }
}
