@import "../../../scss/colors.scss";

#share-canvas {

  .share-banner-container {
    width: 100%;
    img {
      width: 100%;
      min-height: 150px;
    }
  }

  .share-title {
    margin-left: 42px;
    margin-right: 42px;
    margin-top: 30px;
    height: 50px;
    text-align: center;

    p {
      font-family: Montserrat;
      font-size: 20px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.25;
      letter-spacing: normal;
      color: $dark;
    }
  }

  .share-details-list {
    margin-top: 30px;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
  }

  .share-detail-inline {
    margin-top: 22px;
    display: flex;
  }

  .share-detail-number {
    span {
      line-height: 30px;
    }
    min-width: 30px;
    height: 30px;
    margin-right: 14px;
    background-color: $lightGray;
    border-radius: 15px;
    font-family: Montserrat;
    font-size: 16px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.5;
  }

  .share-detail-description {
    margin-top: 3px;
    p {
      text-align: left;
      line-height: 30px;
      margin: 0;
      font-family: Montserrat;
      font-size: 16px;
      font-weight: 100;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal;
      color: $dark;
    }
  }

  .share-switch-container {
    margin-left: auto;
    margin-right: auto;
    width: 80px;
  }
}
