@import "../../../../scss/colors.scss";

.watchlist-item {
  box-sizing: border-box;
  margin-bottom: 10px !important;
  position: relative;
  margin-left: 30px;
  margin-right: 15px;
  padding: 15px 40px 15px 15px;
  display: flex;
  border-radius: 6px;
  box-shadow: 0 5px 20px -5px rgba(177, 186, 197, 0.2);
  border: solid 2px transparent;
  cursor: pointer;

  &.selected {
    border: solid 2px #0070f4;
  }

  .delete-button {
    position: absolute;
    top: 10px;
    right: 10px;

    svg {
      transition: width 0.3s ease;
      visibility: hidden;
      width: 0;
      height: 0;

      &.shown {
        visibility: visible;
        width: 23px;
        height: 23px;
      }
    }
  }

  img {
    object-fit: contain;
    width: 75px;
    min-width: 75px;
    height: 75px;
  }

  .watchlist-item-content {
    display: flex;
    flex-direction: column;
    margin-left: 15px;

    p {
      margin: 0;
      text-align: left;
      font-family: Montserrat;
      font-size: 16px;
      font-weight: 100;
      font-stretch: normal;
      font-style: normal;
      line-height: 20px;
      letter-spacing: normal;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
    }

    .prices {
      margin-top: 15px;
      line-height: 0;
      display: flex;
      flex-direction: row;

      .new-price {
        margin-right: 5px;
        font-family: Montserrat;
        font-size: 18px;
        font-weight: 600;
        color: red;
      }

      .previous-price {
        margin-right: 5px;
        font-family: Montserrat;
        font-size: 14px;
        color: $dark;
        font-weight: 500 !important;

        &.line-through {
          text-decoration: line-through;
        }

        &.main {
          font-size: 18px;
          font-weight: normal;
          font-stretch: normal;
        }
      }

      .pricedrop-value {
        font-family: Montserrat;
        font-size: 14px;
        font-weight: 500;
        font-family: Montserrat;
      }
    }
  }
}
