@import "../../../../scss/colors.scss";

#confirmation-modal {
  display: block;
  position: fixed;
  z-index: 1;
  left: 0;
  bottom: 0;
  height: 210px;
  padding-left: 20px;
  padding-right: 20px;
  border: none;
  opacity: 0;
  border-radius: 6px;
  background-color: transparent;
  transition: visibility 0.3s, opacity 0.3s, transform 0.3s;

  &.fadeOut {
    visibility: hidden;
    opacity: 0;
    transform: translate(0, 80px);
  }

  &.fadeIn {
    opacity: 1;
    transform: translate(0, -80px);
  }

  p {
    margin-top: 0;
    margin-bottom: 0;
  }

  .modal-content {
    padding: 30px;
    position: relative;
    border: none;
    height: 100%;
    background-color: #fefefe;
    box-shadow: 0 0 30px -5px rgba(177, 186, 197, 0.5);
  }

  #close {
    padding: 0;
    margin: 0;
    border: none;
    position: absolute;
    top: 24px;
    right: 24px;
    svg {
      height: 16px;
      width: 16px;
      path {
        fill: $dark;
      }
    }

    &:focus {
      outline: none;
    }
  }

  .close:hover,
  .close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
  }

  .modal-price-drop {
    display: flex;

    svg {
      min-width: 70px;
      height: 70px;
      width: 70px;
      margin-right: 15px;
    }

    .price-drop-notice {
      width: 100%;
      padding-right: 20px;

      p {
        line-height: 24px;
        font-weight: 100;
        font-family: Montserrat;
        font-size: 16px;
        font-stretch: normal;
        text-align: left;
      }
    }
  }

  .download-links {
    position: absolute;
    bottom: 30px;
    widows: 350px;

    a {
      margin-right: 10px;
      margin-left: 10px;

      img {
        width: 158px;
      }
    }
  }

  .store-button {
    width: 160px;
    height: 46.7px;
    color: #000000;
    cursor: pointer;
    border: none;
    &:focus {
      outline: none;
      border: none !important;
    }
  }

  #app-store-button {
    background: url("../../../../scss/svg/apple-appstore-badge-black.svg")
      no-repeat;
    margin-right: 10px;
  }

  #android-store-button {
    background: url("../../../../scss/svg/google-play-badge.svg") no-repeat;
    margin-left: 10px;
  }
}
