@import "../../../scss/colors.scss";

#profile-canvas {
  height: 100%;
  padding: 30px 30px 0 30px;
  overflow-y: overlay;

  &::-webkit-scrollbar {
    width: 15px;
  }

  &::-webkit-scrollbar-track {
    margin-top: 20px;
    background: transparent;
    border-radius: 5px;
    height: 100px;
  }

  &::-webkit-scrollbar-thumb {
    box-shadow: inset 0 0 10px 10px #d1dce7;
    border-left: solid 5px transparent;
    border-right: solid 5px transparent;
    height: 390px;
    border-radius: 5px;
  }

  .user-name {
    font-size: 20px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.2;
    text-align: center;
    letter-spacing: normal;
    color: $dark;
    margin: 0 0 20px 0;
  }

  .download-links {
    margin-top: 25px;

    a {
      margin-right: 10px;
      margin-left: 10px;

      img {
        width: 158px;
      }
    }
  }

  .settings-app-version {
    width: 100%;
    margin-top: 23px;

    p {
      font-size: 14px;
      font-family: Montserrat;
      font-weight: 300;
    }
  }

  .store-button {
    width: 160px;
    height: 46.7px;
    color: #000000;
    cursor: pointer;
    border: none;
    &:focus {
      outline: none;
      border: none !important;
    }
  }

  #app-store-button {
    background: url("../../../scss/svg/apple-appstore-badge-black.svg") no-repeat;
    margin-right: 10px;
  }

  #android-store-button {
    background: url("../../../scss/svg/google-play-badge.svg") no-repeat;
    margin-left: 10px;
  }
}
