@import "../../../scss/colors.scss";
@import "../../../scss/buttons.scss";

button {
  border-radius: 5px;
  cursor: pointer;
}

.canvas {
  position: relative;
  border-radius: 6px;
  background-color: white;
}

.splash {
  width: 100%;
}

.signup-text {
  margin-top: 10px;
  font-family: "Montserrat", sans-serif;
  padding-left: 63px;
  padding-right: 63px;
  height: 44px;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.38;
  letter-spacing: normal;
  text-align: center;
  color: #526173;
}

.activate-text {
  font-family: "Montserrat", sans-serif;
  margin-top: 21px;
  font-size: 20px;
}

#signin-button {
  color: white;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: bold;
  background-color: $blue;
}

#signup-button {
  color: $dark;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: bold;
  border: none;
  background-color: $lightGray;
}

.login-retailer-logo {
  margin-top: 10px;

  &.earny {
    margin-top: 30px;
    margin-bottom: 7px;
  }

  &.earny img {
    width: 100px;
  }
}
