@import "../../../scss/colors.scss";

#watchlist-canvas {
  display: flex;
  flex-direction: column;
  padding-top: 30px;
  position: relative;
  height: 524px;
  overflow: hidden;

  button {
    cursor: pointer;
  }

  &.empty {
    .animation {
      margin-top: 20px;
      margin-bottom: 40px;
      margin-left: auto;
      margin-right: auto;
    }

    p {
      margin: 0 auto 0 auto;
      font-family: Montserrat;
      font-size: 22px;
      font-weight: 700;
      font-stretch: normal;
      color: $dark;
    }

    .add-to-watchlist-steps {
      margin-left: 50px;
      margin-right: 50px;
      display: flex;
      flex-direction: column;

      .watchlist-step-inline {
        margin-top: 22px;
        display: flex;
        flex-direction: row;

        .watchlist-step-number {
          span {
            line-height: 30px;
          }
          min-width: 30px;
          height: 30px;
          margin-right: 14px;
          background-color: $lightGray;
          border-radius: 15px;
          font-family: Montserrat;
          font-size: 16px;
          font-weight: bold;
          font-stretch: normal;
          font-style: normal;
          line-height: 1.5;
        }

        .watchlist-step-description {
          margin-top: 3px;
          p {
            text-align: left;
            line-height: 30px;
            margin: 0;
            font-family: Montserrat;
            font-size: 16px;
            font-weight: 400;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.5;
            letter-spacing: normal;
            color: $dark;
          }
        }
      }
    }
  }

  #list {
    height: 100%;
    overflow-y: scroll;
    transition: all 0.3s ease;

    &.delete-mode {
      height: calc(100% - 60px);
    }

    &::-webkit-scrollbar {
      width: 15px;
    }

    &::-webkit-scrollbar-track {
      background: transparent;
      border-radius: 5px;
      height: 100px;
    }

    &::-webkit-scrollbar-thumb {
      box-shadow: inset 0 0 10px 10px #d1dce7;
      border-left: solid 5px transparent;
      border-right: solid 5px transparent;
      height: 390px;
      border-radius: 5px;
    }
  }

  #edit-button {
    position: absolute;
    right: 30px;
    bottom: 30px;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    border: none;
    box-shadow: 0 8px 20px -5px rgba(177, 186, 197, 0.5);
    transition: all 0.3s ease;

    &.cancel {
      bottom: 90px;
    }
  }

  #remove-button {
    width: 100%;
    height: 60px;
    position: absolute;
    bottom: -60px;
    color: #b1bac5;
    background-color: #eef2f7;
    font-family: Montserrat;
    font-size: 16px;
    font-weight: 500;
    border-radius: 0;
    border: none;
    transition: all 0.3s ease;

    &.enabled {
      color: #ffffff;
      background-color: $blue;
    }

    &.shown {
      bottom: 0;
    }
  }
}
