@import '../../../../scss/colors.scss';

#cashout-description {
  margin-top: 20px;
  padding: 15px 17px 15px 17px;
  display: flex;
  flex-direction: row;
  border-radius: 6px;
  background-color: $lightGray;

  img {
    min-width: 70px;
    width: 70px;
    height: 70px;
  }

  .cashout-texts {
    margin-left: 15px;
    text-align: left;

    p.cashout-title {
      margin: 4px 0 5px 0;
      font-size: 16px;
      font-weight: 600;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      color: $dark;
    }

    p.cashout-description {
      margin: 0;
      font-size: 14px;
      font-weight: 400;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.43;
      letter-spacing: normal;
      color: #526173;

      a {
        text-decoration: underline;
        color: #526173;
      }
    }
  }
}

button#cashout {
  font-weight: 600;
  border: none;
  margin-top: 20px;
  height: 60px;
  width: 100%;
  border-radius: 6px;
  background-color: #ebeef0;
  font-size: 18px;
  text-align: center;
  color: #b1bac5;
  font-family: Montserrat;

  &.can-cashout {
    background-color: $blue;
    font-weight: bold;
    color: white;
  }
}
