@import "../../../../scss/colors.scss";

.profile-card {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  box-shadow: 0 5px 20px -5px rgba(177, 186, 197, 0.2);
  border-radius: 6px;
  background-color: white;
  overflow: hidden;

  .banner {
    position: relative;
    padding-bottom: 20px;

    &.with-animation {
      margin-bottom: 20px;
    }

    img {
      width: 100%;
    }

    .lottie {
      position: absolute;
      bottom: 0;
      left: calc(50% - 55px);
      width: 110px;
      height: 110px;
    }
  }

  .card-content {
    padding: 0 26px 30px;

    h3 {
      margin: 0;
      font-size: 20px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.2;
      letter-spacing: normal;
      text-align: center;
      color: $dark;
    }

    p {
      font-size: 16px;
      font-weight: 300;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.38;
      letter-spacing: normal;
      text-align: center;
      color: #526173;
    }

    button.cta-button {
      padding-left: 2px;
      padding-right: 2px;
      width: 100%;
      height: 60px;
      border-radius: 6px;
      background-color: $blue;
      font-family: Montserrat;
      font-size: 18px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: center;
      color: #ffffff;
    }
  }
}
