#cashback-list-component {
  overflow-y: overlay;
  padding: 30px;
  height: 100%;

  p.cashback-list-title {
    text-align: center;
    font-family: Montserrat;
    font-size: 16px;
    font-weight: 100;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.5;
    letter-spacing: normal;
    text-align: center;
  }

  #cashback-retailer-list {
    padding-top: 10px;
    height: 100%;
    grid-template-columns: repeat(2, 1fr);
    display: grid;
    grid-auto-rows: min-content;

    .cashback-list-retailer-item {
      padding: 0;
      width: 100%;

      button {
        border: none;
        cursor: pointer;
        background-color: white;
      }

      img {
        border-radius: 6px;
        box-shadow: 0 6px 23px -6px rgba(177, 186, 197, 0.2);
        width: 100%;
      }

      p {
        font-family: Montserrat;
        font-size: 16px;
        font-weight: 500;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.5;
        letter-spacing: normal;
        color: #19a388;
        margin: 10px auto 20px auto;
      }
    }
  }
}
