a,
u {
  text-decoration: none;
}

* {
  font-family: Montserrat;
}

body {
  display: table;
  background-color: #eaeaea;
  text-rendering: geometricPrecision;
}

div {
  box-sizing: border-box;
}

#root {
  display: table;
}

.Earny {
  position: relative;
  height: 100%;
  width: 100%;
  text-align: center;
}

#earny-loader {
  position: relative;
  top: 50%;
  margin: auto;
}

#boot-container {
  background-color: white;
  width: 450px;
  height: 584px;
}

.recharts-tooltip-wrapper {
  font-family: "Montserrat";
}

.recharts-default-tooltip {
  border: none;
  border-radius: 3px;
  box-shadow: 0px 8px 20px -5px rgba(177, 186, 197, 0.5);
}
