@import "../../../../scss/colors.scss";

#earny-link-box {
  position: relative;
  margin-top: 20px;
  border-radius: 6px;
  overflow: hidden;
  background-color: $lightGray;

  h3 {
    margin: 30px 0 0 30px;
    position: absolute;
    width: 200px;
    text-align: left;
    font-size: 20px;
    font-weight: 800;
    line-height: 1.2;
    color: white;
    z-index: 10;
  }

  .bottom {
    p.friend-referral {
      margin: 13px 30px 18px 30px;
      text-align: left;
      font-size: 15px;
      font-weight: 300;
      line-height: 1.38;
      color: $dark;

      a {
        text-decoration: underline;
        color: $dark;
      }
    }

    .copy-link {
      margin: 18px 15px 15px 15px;
      height: 50px;
      display: flex;
      flex-direction: row;
      border-radius: 6px;
      box-shadow: 0 5px 20px -5px rgba(177, 186, 197, 0.2);
      background-color: white;

      .link {
        padding-left: 20px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: left;
        width: 100%;
        font-size: 16px;
        color: $dark;
      }

      button.copy-button {
        border: none;
        font-size: 16px;
        font-weight: 700;
        text-align: center;
        color: $dark;
        border: none;
        min-width: 90px;
        width: 90px;
        border-radius: 6px;
        box-shadow: -3px 0 20px 0 rgba(177, 186, 197, 0.2);
        background-color: #ffffff;

        &.copied {
          color: #19a388;
        }
      }
    }

    .social-media {
      margin: 0px 30px 5px 30px;
      display: flex;
      padding-bottom: 15px;

      button {
        padding: 0;
        border: none;
        background-color: transparent;

        svg {
          max-width: 25px;
          margin-right: 25px;
        }
      }
    }
  }
}
