@import "../../../scss/colors.scss";

#product-details-canvas {
  position: relative;
  padding: 30px;

  #previous-button {
    cursor: pointer;
    line-height: 16px;
    position: absolute;
    top: 28px;
    left: 20px;
    svg {
      height: 16px;
      width: 16px;
      path {
        fill: $dark;
      }
    }
  }

  .product-details {
    min-height: 80px;
    display: flex;

    img {
      object-fit: contain;
      width: 80px !important;
      height: 80px;
      min-width: 80px;
    }

    .product-content {
      max-width: 300px;
      margin-left: 15px;
      text-align: left;

      .product-name {
        p {
          font-family: Montserrat;
          font-size: 16px;
          font-weight: 600;
          font-stretch: normal;
          font-style: normal;
          line-height: 1.38;
          color: $dark;
          padding: 0;
          margin: 0;
          overflow: hidden;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
      }
      .product-prices {
        margin-top: 5px;
        display: flex;

        .product-new-price {
          margin-right: 15px;
          font-size: 18px;
          font-weight: bold;
          font-stretch: normal;
          font-style: normal;
          line-height: 1.33;
          letter-spacing: normal;
          color: #ee0000;
        }

        .product-old-price {
          font-size: 14px;
          font-weight: 400;
          color: $dark;
          text-decoration: line-through;
        }

        button#save-button {
          width: 23px;
          height: 20px;
          outline: none;
          border: none;
          padding: 0;
          margin-top: 1px;
          line-height: 0;
          background-color: none;

          &.saved {
            path#save-path {
              // stroke: none;
              stroke: #ff6060;
              // stroke-width: 1px;
              fill: #ff6060;
            }
          }
        }
      }
    }
  }

  .product-time-to-buy {
    margin-top: 15px;
    display: flex;
    height: 60px;
    box-shadow: 0 5px 20px -5px rgba(177, 186, 197, 0.25);

    span {
      font-family: Montserrat;
      font-size: 14px;
      font-weight: 500;
      line-height: 58px;

      &.left {
        margin-left: 25px;
        margin-right: 17px;
      }
      &.right {
        margin-left: 17px;
        margin-right: 25px;
      }
    }
  }

  #price-stats {
    margin-top: 10px;
    display: flex;
  }

  .price-stat {
    display: flex;
    flex-direction: column;
    width: 50%;
    padding: 11px 0 11px 0;
    height: 60px;
    box-shadow: 0 5px 20px -5px rgba(177, 186, 197, 0.25);

    &.left {
      margin-right: 5px;
    }

    &.right {
      margin-left: 5px;
    }

    p {
      font-family: Montserrat;
      font-size: 12px;
      font-weight: 600;
      font-stretch: normal;
      font-style: normal;
      letter-spacing: normal;
      color: #b1bac5;
      padding: 0;
      margin: 0 auto 0 auto;
    }

    .price {
      margin-top: 5px;
      color: $dark;
      font-family: Montserrat;
      font-size: 14.5px;
      font-weight: 600;
    }
  }

  .recharts-wrapper {
    margin: 0 auto;
  }

  .price-tracking-earny-logo img {
    min-height: 43px;
    width: 100px;
    height: auto;
    margin-bottom: 15px;
  }

  .pdp-no-product {
    margin-top: 58px;

    img {
      width: 154px;
    }
  }

  .pdp-no-product-description {
    margin-top: 20px;
    margin-left: 48px;
    margin-right: 48px;

    p {
      font-family: Montserrat;
      font-size: 22px;
      font-weight: 600;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.27;
      color: $dark;
    }
  }

  .no-product-details {
    margin-top: 140px;

    p.add-to-watchlist-title {
      margin-bottom: 0;
      margin-left: 35px;
      margin-right: 35px;
      font-family: Montserrat;
      font-size: 22px;
      font-weight: 600;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.27;
      letter-spacing: normal;
      text-align: center;
      color: $dark;
    }

    p.watchlist-information-title {
      margin-top: 10px;
      margin-bottom: 25px;
      font-family: Montserrat;
      font-size: 16px;
      font-weight: 300;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal;
      text-align: center;
      color: $dark;
    }

    button#add-to-watchlist {
      height: 60px;
      width: 100%;
      border-radius: 6px;
      background-color: $blue;
      font-family: Montserrat;
      font-size: 18px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: center;
      color: #ffffff;
    }
  }

  .product-details-pending {
    margin-top: 135px;

    p.loading-product-details-title {
      font-family: Montserrat;
      font-size: 22px;
      font-weight: 600;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.27;
      letter-spacing: normal;
      text-align: center;
      color: $dark;
    }

    p.information-title {
      font-family: Montserrat;
      font-size: 16px;
      font-weight: 300;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal;
      text-align: center;
      color: $dark;
    }
  }
}
