@import "../../../../scss/colors.scss";

#retailer-activated {
  overflow-y: overlay;
  padding: 30px;
  height: 100%;

  .header {
    width: 100%;
    height: 60px;
    display: flex;
    background-color: $lightGray;
    border-radius: 6px;
    margin-top: 0px;
    margin-bottom: 30px;

    img {
      margin-left: 30px;
      width: 32px;
    }

    p {
      margin: 0 0 0 10px;
      line-height: 60px;
      vertical-align: middle;
      font-family: Montserrat;
      font-size: 18px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      letter-spacing: normal;
      text-align: center;
      color: $dark;
    }
  }

  .rate {
    margin-bottom: 10px;
    text-align: left;
    padding: 12px 15px;
    display: flex;
    flex-direction: column;
    border-radius: 6px;
    box-shadow: 0 5px 20px -5px rgba(177, 186, 197, 0.2);
    background-color: #ffffff;

    h3.rate-name {
      margin: 0 0 4px 0;
      font-size: 15px;
      font-weight: 600;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal;
      color: #232e3b;
    }

    .rate-value {
      font-size: 15px;
      font-weight: 600;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      color: #19a388;
    }
  }

  .cashback-exclusion {
    border-radius: 6px;
    box-shadow: 0 5px 20px -5px rgba(177, 186, 197, 0.2);
    background-color: #ffffff;
    margin-top: 20px;
    padding: 20px;

    p {
      font-family: Montserrat;
      font-size: 16px;
      font-weight: 400;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      color: black;
      margin: 0;
      text-align: left;
    }
  }
}
