@import "../../../scss/colors.scss";
@import "../../../scss/buttons.scss";


#home-canvas {
  position: relative;
  height: 100%;
  padding: 30px;

  &.scroll-child {
    display: flex;
    flex-direction: column;
    padding: 0;
  }

  &::-webkit-scrollbar {
    width: 15px;
  }

  &::-webkit-scrollbar-track {
    margin-top: 20px;
    background: transparent;
    border-radius: 5px;
    height: 100px;
  }

  &::-webkit-scrollbar-thumb {
    box-shadow: inset 0 0 10px 10px #d1dce7;
    border-left: solid 5px transparent;
    border-right: solid 5px transparent;
    height: 390px;
    border-radius: 5px;
  }

  #activateButton {
    margin: 25px auto 25px;
  }

  #cashbackText {
    margin-top: 25px;
  }

  #usernameText {
    margin-top: 10px;
  }

  #img-container {
    display: block;
    text-align: center;
  }
  .container {
    width: 175px;
  }

  .home-retailer-logo img {
    margin-left: 20px;
    width: 124px;
    height: auto;
    min-height: 80px;
  }

  .home-earny-logo img {
    min-height: 43px;
    width: 100px;
    height: auto;
    margin-bottom: 15px;
  }

  .home-activate-button {
    margin-top: 10px;
    padding: 0 25px;
    height: 60px;
    width: 100%;
    border-radius: 6px !important;
    background-color: #0070f4;
    font-family: Montserrat;
    font-size: 16px;
    font-weight: bold !important;
    border-style: none;
    color: white;
  }

  .home-activate-text {
    width: 100%;
    display: flex;
    border-radius: 6px;
    margin-top: 20px;
    padding: 18px 25px 18px 25px;
    background-color: #f7f9fb;

    img {
      width: 36px;
      height: 44px;
      margin-top: -3px;
      margin-right: 18px;
    }

    p {
      margin: 0;
      text-align: left;
      font-family: Montserrat;
      font-size: 16px;
      font-weight: 400;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.38;
      letter-spacing: normal;
      color: #526173;
    }
  }

  .home-no-cashback {
    margin-top: 58px;

    img {
      width: 154px;
    }
  }

  .home-no-cashback-description {
    margin-top: 20px;
    margin-left: 48px;
    margin-right: 48px;

    p {
      font-family: Montserrat;
      font-size: 22px;
      font-weight: 600;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.27;
      color: $dark;
    }
  }



  .activate-double-cashback {
    position: absolute;
    display: flex;
    padding-left: 30px;
    padding-right: 30px;
    flex-direction: row;
    align-items: center;
    width: 100%;
    left: 0;
    bottom: 0;
    height: 60px;
    box-shadow: 0 10px 20px -5px rgba(177, 186, 197, 0.2);
    background-color: #8100cc;

    span.double-cashback-title {
      font-family: Montserrat;
      font-size: 18px;
      font-weight: 600;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      color: #ffffff;
      margin-right: 40px;
    }

    .switch {
      cursor: pointer;
      display: flex;
      padding: 6px;
      align-items: center;
      flex-direction: row;
      width: 70px;
      height: 36px;
      border-radius: 18px;
      background-color: #4d0079;

      .switch-ball {
        width: 24px;
        height: 24px;
        border-radius: 50%;
        background-color: white;
      }
    }
  }
}
