.button-bar-two {
    display: flex;
    position: absolute;
    left: 0px;
    right: 0px;

    button {
        height: 50px;
        width: 50%;

        &.left {
            margin-right: 8px;
        }

        &.right {
            margin-left: 8px;
        }
    }
}

button:focus {
    outline: none;
    border: none;
}

button a {
    display: inline-block;
    width: 100%;
    text-decoration: none !important;
}