.bottom-navigation {
  text-align: center;
  height: 60px;
  background-color: #f7f9fb;
}

.bottom-nav-menu-item {
  display: inline-block;
  height: 100%;
  margin-left: 27px;
  margin-right: 27px;

  img {
    width: 32px;
    height: 32px;
    margin-top: 15px;
  }
}

.navlink-active img {
  filter: brightness(0) saturate(100%) invert(15%) sepia(12%) saturate(1326%) hue-rotate(172deg) brightness(97%) contrast(92%);
}
